<template>
    <div class="page-container">
        <div class="header">
            <!-- Your other content (calendars, buttons, etc.) -->
            <div
                class="p-mx-2 p-mb-3"
                style="display: flex; justify-content: space-between;"
            >
                <div style="display: flex;">
                    <div class="p-field p-grid">
                        <label for="date" class="p-col-fixed">Date</label>
                        <div>
                            <p-calendar
                                id="date"
                                v-model="selectedDate"
                                dateFormat="yy-mm-dd"
                                :showIcon="true"
                                class="custom-calendar"
                            />
                        </div>
                    </div>
                    <p-button
                        label="Get Data"
                        class="p-ml-4"
                        @click="fetchData"
                    />
                </div>
                <div style="display: flex;">
                    <div class="p-field p-grid p-mr-4">
                        <label for="fromDate" class="p-col-fixed"
                            >From Date</label
                        >
                        <div>
                            <p-calendar
                                id="fromDate"
                                v-model="fromDate"
                                dateFormat="yy-mm-dd"
                                :showIcon="true"
                                class="custom-calendar"
                            />
                        </div>
                    </div>
                    <div class="p-field p-grid">
                        <label for="toDate" class="p-col-fixed">To Date</label>
                        <div>
                            <p-calendar
                                id="toDate"
                                v-model="toDate"
                                dateFormat="yy-mm-dd"
                                :showIcon="true"
                                class="custom-calendar"
                            />
                        </div>
                    </div>
                    <p-button
                        label="Export XLS"
                        class="p-ml-4"
                        @click="exportExcel"
                    />
                </div>
            </div>
        </div>

        <div class="table-container">
            <p-data-table
                :value="labMonForm"
                showGridlines
                class="p-datatable-sm p-mt-1 p-shadow-1"
                :scrollable="true"
                scrollDirection="both"
                scrollHeight="flex"
            >
                <!-- Table Columns and Body -->
                <template v-for="(column, index) in columns" :key="index">
                    <p-column
                        :field="column"
                        :header="
                            head_titles[column]
                                ? head_titles[column]
                                : column.charAt(0).toUpperCase() +
                                  column.slice(1)
                        "
                        :style="{ width: '200px' }"
                    >
                        <template #body="slotProps">
                            <div class="cell-content">
                                <span
                                    v-if="['name'].includes(column)"
                                    class="static-text"
                                >
                                    {{ slotProps.data[column].val }}
                                    {{
                                        slotProps.data[column].ref
                                            ? "[" +
                                              slotProps.data[column].ref +
                                              "]"
                                            : ""
                                    }}
                                </span>

                                <p-input-text
                                    v-else
                                    :value="slotProps.data[column].val"
                                    class="custom-input-text"
                                    @change="
                                        updateValue(
                                            $event,
                                            column,
                                            slotProps.index
                                        )
                                    "
                                />
                                <div
                                    v-if="!['name'].includes(column)"
                                    class="cell-content2"
                                >
                                    <span class="static-text">{{
                                        slotProps.data[column].time
                                    }}</span>
                                    <span class="static-text">{{
                                        slotProps.data[column].initial
                                    }}</span>
                                </div>
                            </div>
                        </template>
                    </p-column>
                </template>
            </p-data-table>
        </div>
    </div>
</template>

<script>
import { ref, onMounted, watch } from "vue";
import { $axios } from "@/service/app-http";
import { ORIGIN } from "../config.js";

export default {
    setup() {
        const labMonForm = ref([]);
        const selectedDate = ref(new Date().toISOString().split("T")[0]); // Initialize in 'yyyy-mm-dd' format
        const fromDate = ref(new Date().toISOString().split("T")[0]);
        const toDate = ref(new Date().toISOString().split("T")[0]);
        const columns = ref([]);
        const head_titles = ref({
            co2: "Co2 (Ref: 6% - 7%)",
            temperature: "Temperature (Ref: 37+ - 0.5)",
            water_level: "Water Level (Ref: => 40%)",
            ln_level: "Ln Level",
            liquid_usagae: "Liquid Usage",
            ph: "PH (Ref: 7.22 - 7.38)"
        });

        const formatDate = (date) => {
            const d = new Date(date);
            let month = "" + (d.getMonth() + 1);
            let day = "" + d.getDate();
            const year = d.getFullYear();

            if (month.length < 2) month = "0" + month;
            if (day.length < 2) day = "0" + day;

            return [year, month, day].join("-");
        };

        const fetchData = async () => {
            try {
                const formattedDate = formatDate(selectedDate.value); // Format the date
                const formattedFromDate = formatDate(fromDate.value);
                const formattedToDate = formatDate(toDate.value);

                const response = await $axios.get("lab-mon/data", {
                    params: { date: formattedDate }
                });

                labMonForm.value = response.data;
                columns.value = Object.keys(labMonForm.value[0] || {});
            } catch (error) {
                console.error("Error fetching data:", error);
            }
        };

        const updateValue = async (event, column, rowIndex) => {
            if (rowIndex >= 0 && rowIndex < labMonForm.value.length) {
                labMonForm.value[rowIndex][column].val = event.target.value;

                const formattedDate = formatDate(selectedDate.value);
                const formattedFromDate = formatDate(fromDate.value);
                const formattedToDate = formatDate(toDate.value);

                const { data } = await $axios.post("lab-mon/save", {
                    name: labMonForm.value[rowIndex]["name"].val,
                    value: labMonForm.value[rowIndex][column].val,
                    colName: column,
                    date: formattedDate
                });

                labMonForm.value[rowIndex][column].time = data.time;
                labMonForm.value[rowIndex][column].initial = data.initial;
            } else {
                console.error(`Invalid rowIndex: ${rowIndex}`);
            }
        };

        const exportExcel = async () => {
            try {
                const formattedFromDate = formatDate(fromDate.value);
                const formattedToDate = formatDate(toDate.value);

                const response = await $axios.get("lab-mon/export", {
                    params: {
                        fromDate: formattedFromDate,
                        toDate: formattedToDate
                    }
                });

                if (response.data && response.data.filePath) {
                    const fileUrl = `${ORIGIN}/${response.data.filePath}`;

                    // Open the file in a new tab
                    window.open(fileUrl, "_blank");
                } else {
                    console.error("Export link not found in response.");
                }
            } catch (error) {
                console.error("Error exporting data:", error);
            }
        };

        watch(selectedDate, fetchData);

        onMounted(() => {
            fetchData();
        });

        return {
            labMonForm,
            columns,
            head_titles,
            selectedDate,
            fromDate,
            toDate,
            fetchData,
            updateValue,
            exportExcel
        };
    }
};
</script>

<style scoped>
.page-container {
    display: flex;
    flex-direction: column;
    height: 100vh; /* Full viewport height */
}

.header {
    flex-shrink: 0; /* Prevents the header from shrinking */
    padding: 10px;
    background-color: #f0f0f0;
}

.table-container {
    flex-grow: 1; /* Make the table container fill the remaining space */
    display: flex;
    flex-direction: column;
    overflow: hidden; /* Ensure no overflow issues */
}

.p-datatable-sm {
    flex-grow: 1; /* Allow the table to grow and fill available space */
    overflow-y: auto; /* Enable scrolling within the table body */
}

.cell-content {
    display: flex;
    flex-direction: column;
    align-items: left;
}

.cell-content2 {
    display: flex;
    justify-content: space-between;
    margin-top: 4px;
    font-size: 0.85rem;
}

.static-text {
    margin-left: 6px;
}
.custom-input-text {
    background-color: transparent;
    border: none;
    width: 100%;
}
.custom-calendar {
    width: 130px;
}
</style>
